.footer{
  display: none;
}

@media screen and (max-width: 800px) and (min-width: 260px) {
    .footer {
      /* background-image: url(../../assests/snn1.webp); */
      background: #000000;
      color: #ffffff;
      font-family: Comic Sans MS;
      display: block;
    }
  
    .div1 {
      padding: 9.74vh 0 6vh 5vw;
    }
  
    .mob-footer-logo {
      width: 99.4px;
      height: 64px;
    }
  
    .direction {
      margin: 7.9vh 0;
    }
  
    .heading {
      margin-bottom: 3vh;
      font-size: 1rem;
      font-weight: 700;
      line-height: 1.4rem;
      letter-spacing: 0.05em;
      text-align: left;
    }
  
    .direction p {
      font-size: 0.875rem;
      font-style: normal;
      font-weight: 400;
      line-height: 160%;
      letter-spacing: 0.7px;
      width: 60%;
    }
  
    .connect a span {
      font-size: 0.875rem;
      font-weight: 400;
      line-height: 1.25rem;
      letter-spacing: 0.05em;
      text-align: left;
    }
  
    .mail a img {
      position: relative;
      width: 16.7px;
      height: 13.3px;
      top: 3.3px;
      left: 1.7px;
      margin-right: 8px;
    }
  
    .phone a img {
      position: relative;
      width: 16px;
      height: 16px;
      top: 2px;
      left: 2px;
      margin-right: 8px;
    }
  
    .para1,
    .para3 {
      margin-bottom: 1.8vh;
    }
  
    .para2 {
      margin-bottom: 4.87vh;
    }
  
    .div2 hr {
      width: 90vw;
      opacity: 20%;
    }
  
    .social-links {
      padding: 3.8vh 14.4vw 6vh;
      display: flex;
      justify-content: space-between;
    }
  
    .link a img {
      height: 32px;
      width: 32px;
    }
  
    a {
      color: inherit;
      text-decoration: none;
    }
  }
  