#card_icc {
  cursor: pointer;
  box-sizing: border-box;
  background: radial-gradient(
    111.43% 111.43% at 0% 0%,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.1) 100%
  );
  /* backdrop-filter: blur(42px); */
  width: 250px;
  margin-bottom: 24px;
  border: 1.5px solid transparent;
  border-image-source: radial-gradient(
    115.98% 168.03% at -3.2% 0%,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.6) 100%
  );
  border-image-slice: 1;
  transition-duration: 500ms;
}
#card_icc:hover{
  border-image-source: radial-gradient(
    115.98% 168.03% at -3.2% 0%,
    rgba(255, 255, 255, 0.6) 0%,
    rgba(255, 255, 255, 0.05) 100%
  );
}

#img_1__card_ec {
  width: 226px;
  height: 200px;
  margin: 12px;
  object-fit: cover;
  margin-bottom: 16px !important;
}

#text_1_card_ec_1 {
  font-family: "Oxanium";
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  /* identical to box height, or 100% */

  display: flex;
  align-items: center;
  letter-spacing: -0.02em;

  color: #e9e9e9;
  margin-bottom: 2px;
  margin-left: 12px;
}

#text_1_card_ec_2 {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  /* identical to box height, or 114% */

  display: flex;
  align-items: center;
  letter-spacing: -0.02em;

  color: #ffffff;
  margin-bottom: 12px;
  margin-left: 12px;
}
.none{
  display: none;
}
@media screen and (max-width:768px){
  #card_icc{
    width: 300px;
  }
  /* /* #img_1__card_ec{ */
    /* width: 74vw;
  } */
  #img_1__card_ec {
    width: 275px;
    height: auto;
    margin: 10px;
    margin-bottom: 10px !important;
  }
}

.paynow{
  width: 100%;
  height: 44px;
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  border: 2px solid rgb(255, 255, 255);
  border-radius: 4px;
  color: #ffffff;
  background: #21827e;
  cursor: pointer;
  text-decoration: none;
  padding: 8px 15px;
  text-align: center;
}

.paynow:hover {
  background: #21827e;
  border: none;
  padding: 10px 20px;
  text-align: center;
}